import { Injectable } from '@angular/core';
import { catchError, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { HttpResponse, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Cookie } from 'ng2-cookies/ng2-cookies';

@Injectable({
  providedIn: 'root'
})
export class AppService {
  private url = "http://api.thesoulblog.in/api/v1";
  constructor(public http: HttpClient) { }
  //user sign up function
  public signupFunction(data): Observable<any> {
    const params = new HttpParams()
      .set('firstName', data.firstName)
      .set('lastName', data.lastName)
      .set('mobileNumber', data.mobileNumber)
      .set('email', data.email)
      .set('password', data.password)
      .set('coutryCode', data.coutryCode)
      .set('userName', data.userName)
      .set('isAdmin', data.isAdmin)

    return this.http.post(this.url + '/users/signup', params);
  }
  //user signin funtion
  public signinFunction(data): Observable<any> {
    const params = new HttpParams()
      .set('email', data.email)
      .set('password', data.password)

    return this.http.post(this.url + '/users/login', params);
  }
  //user recoverpassword function
  public recoverPasword(email): Observable<any> {
    const params = new HttpParams()
      .set('email', email)


    return this.http.post(this.url + '/users/forgetPassword', params);
  }
  //set user info in local storage by setter function
  public setUserInfoIntoLocalStorage = (data): any => {
    localStorage.setItem('userInfo', JSON.stringify(data));
  }
  //get user info in local storage by setter function
  public getUserInfoIntoLocalStorage = () => {
    return JSON.parse(localStorage.getItem('userInfo'));
  }
  //get all events of particular normal user
  public getNormalUserAllEvent = (userId) => {
    const parmas = new HttpParams()
      .set('userId', userId)
    return this.http.post(this.url + '/meeting/getuserallmeeting', parmas);
  }
  //end
  //get all events of admin user
  public getAdminAllEvent = (userId) => {
    const parmas = new HttpParams()
      .set('adminId', userId)
    return this.http.post(this.url + '/meeting/getadminallmeeting', parmas);
  }
  //end

  //get all userlist
  public getAllUserList = () => {
    return this.http.get(this.url + '/users/getalluser');
  }
  //end

  //add user event to database
  public addUserEvent = (data) => {
    //console.log('add');
    const params = new HttpParams()
      .set('adminName', data.adminName)
      .set('adminId', data.adminId)
      .set('userId', data.userId)
      .set('title', data.title)
      .set('start', data.start)
      .set('end', data.end)

    return this.http.post(this.url + '/meeting/addusermeeting', params);
  }
  //end

  //edit metting details
  public editmeeting = (data) => {
    console.log('add');
    const params = new HttpParams()
      .set('_id', data._id)
      .set('title', data.title)
      .set('start', data.start)
      .set('end', data.end)

    return this.http.post(this.url + '/meeting/editmeeting', params);
  }
  //end

  //delete user event from database
  public deleteEvent = (_id) => {
    const parmas = new HttpParams()
      .set('_id', _id)
    return this.http.post(this.url + '/meeting/deleteusermeeting/', parmas);
  }
  //end

  //inform user by email 
  public informUserByMail = (data) => {
    const params = new HttpParams()
      .set('adminName', data.adminName)
      .set('userId', data.userId)
      .set('title', data.title)

    return this.http.post(this.url + '/meeting/informbyemail', params);
  }
  //
  //user logout function
  public logout = (): Observable<any> => {
    console.log("logout called")
    const params = new HttpParams()
      .set('userId', Cookie.get('receiverId'))
    return this.http.post(this.url + '/users/logout', params);
  }

}
