import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrManager } from 'ng6-toastr-notifications';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { AppService } from 'src/app/app.service';
import { SocketService } from 'src/app/socket.service';

@Component({
  selector: 'app-admindashboard',
  templateUrl: './admindashboard.component.html',
  styleUrls: ['./admindashboard.component.css']
})
export class AdmindashboardComponent implements OnInit {

  @ViewChild('scrollMe') read: ElementRef;
  public active: boolean = false;
  public scrollMe: ElementRef;
  public scrollToTop: boolean = false;
  public authToken: any;
  public userInfo: any;
  public receiverId: any;
  public receiverName: any;
  public userList: any = [];
  public alluserList: any = [];
  public disconnectedSoket: boolean;
  public userSelectId: any;
  public userSelectName: any;
  constructor(public router: Router,
    public toastr: ToastrManager,
    public appService: AppService,
    public socketService: SocketService) {

  }



  ngOnInit() {
    console.log('ngint')
    let userDetail = this.appService.getUserInfoIntoLocalStorage();
    let isUserAdmin = (userDetail.isAdmin == "admin") ? true : false;
    if (!isUserAdmin) {
      this.router.navigate(['/normaluserdashboard'])
    }
    this.authToken = Cookie.get('authToken');
    this.receiverId = Cookie.get('receiverId');
    this.receiverName = Cookie.get('receiverName');
    this.userSelectId = this.receiverId;
    this.userSelectName = this.receiverName;
    this.userInfo = this.appService.getUserInfoIntoLocalStorage();
    this.checkStatus();
    this.verifyUserConfirmation();
    this.getOnlineUserList();
    this.getAllUserList();
  }

  //AdminCalender
  public AdminCalender: any = () => {
    location.reload();
  }

  //navigate
  public goToMeeting: any = () => {
    return this.router.navigate(['/adminuserdashboard']);
  }
  //
  //toggle sidebar function
  public toggleBar: any = () => {
    console.log('click');
    if (this.active) {
      this.active = false;
    } else {
      this.active = true;
    }
    console.log(this.active)
  }
  //end

  //function to check authtoken
  public checkStatus: any = () => {
    if (Cookie.get('authToken') === undefined || Cookie.get('authToken') === '' || Cookie.get('authToken') === null) {
      this.router.navigate(['/']);
      return false;
    }
    else {
      return true;
    }
  }
  //end of checkstatus

  //function to verifyuser
  public verifyUserConfirmation: any = () => {
    this.socketService.setUser(this.authToken);
    this.socketService.verifUser().subscribe((data: any) => {
      this.disconnectedSoket = false;
      console.log('verified user');
      this.socketService.setUser(this.authToken);

    });
  }
  //end 

  //function to get all online user list
  public getOnlineUserList: any = () => {
    this.socketService.onlineUserList().subscribe(
      (userList) => {
        this.userList = [];
        for (let x in userList) {
          let temp = { 'userId': x, 'name': userList[x] };
          this.userList.push(temp);
        }
        console.log('userlist', this.userList);
      }
    );
  }
  //end

  public isEmpty(obj) {
    for (let key in obj) {
      if (obj.hasOwnProperty(key))
        return false;
    }
    return true;
  }

  //function to get all user in application
  public getAllUserList: any = () => {
    this.appService.getAllUserList().subscribe(
      (userList: any) => {
        this.alluserList = [];
        for (let x of userList) {
          if (x.userId != this.receiverId) {
            let temp = { 'userId': x.userId, 'name': x.firstName + ' ' + x.lastName };
            this.alluserList.push(temp);
          }
        }
        console.log('userlist', this.alluserList);
      },
      error => {
        console.log('error occured in getalluserList');
      }
    )
  }
  //end 

  //userselected for meeting
  public userSelectedToMetting = (userId, name) => {
    console.log('userselected', userId)
    this.userSelectId = userId;
    this.userSelectName = name;
    this.active = false;
  }
  //end
  public logout: any = () => {
    console.log("logout called")
    this.appService.logout()
      .subscribe((apiResponse) => {

        if (apiResponse.status === 200) {
          console.log("logout called")
          Cookie.delete('authToken');

          Cookie.delete('receiverId');

          Cookie.delete('receiverName');



          this.socketService.exitSocket()

          this.router.navigate(['/']);

        } else {
          this.toastr.errorToastr(apiResponse.message)

        } // end condition

      }, (err) => {
        this.toastr.errorToastr('some error occured')


      });

  }

}
