import { Component, ChangeDetectionStrategy, ViewChild, TemplateRef, OnInit, Input, SimpleChanges } from '@angular/core';
import {
  startOfDay, endOfDay, subDays, addDays, endOfMonth, isSameDay, isSameMonth, addHours
} from 'date-fns';
import { Subject } from 'rxjs';
import { CalendarEvent, CalendarEventAction, CalendarEventTimesChangedEvent, CalendarView } from 'angular-calendar';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrManager } from 'ng6-toastr-notifications';
import { AppService } from 'src/app/app.service';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { SocketService } from 'src/app/socket.service';

const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};
@Component({
  selector: 'app-admin-user',
  templateUrl: './admin-user.component.html',
  styleUrls: ['./admin-user.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AdminUserComponent implements OnInit {
  @ViewChild('modalContent') modalContent: TemplateRef<any>;
  view: CalendarView = CalendarView.Month;
  public startDate: any;
  public startTime: any;
  public endDate: any;
  public endTime: any;
  public title: any;
  public adminId: any;
  @Input('userId') userId: any;
  @Input('userName') userName: any;
  public adminName: any;
  CalendarView = CalendarView;

  viewDate: Date = new Date();

  modalData: {
    action: string;
    event: CalendarEvent;
  };
  modalRef: BsModalRef;


  refresh: Subject<any> = new Subject();
  events: CalendarEvent[] = [];

  activeDayIsOpen: boolean = true;
  constructor(private modalService: BsModalService, private appService: AppService,
    private toastr: ToastrManager, public socketService: SocketService) { }

  ngOnInit() {
    this.adminName = Cookie.get('receiverName');
    this.adminId = Cookie.get('receiverId');
    this.getUserAllEvent(this.userId);

    console.log('check' + Date());
  }
  ngOnChanges(changes: SimpleChanges) {
    this.events = [];
    this.getUserAllEvent(changes.userId.currentValue);
    // You can also use categoryId.previousValue and 
    // categoryId.firstChange for comparing old and new values

  }
  //get all metting of particular user
  public getUserAllEvent = (userId) => {
    console.log('userid' + userId + 'adminId' + this.adminId)
    if (userId != this.adminId) {
      console.log('normal')
      this.appService.getNormalUserAllEvent(userId).subscribe((data: any) => {
        for (let item of data) {
          item['color'] = colors.blue;
          console.log('start-----', item.start);
          item.start = new Date(item.start);
          item.start.setHours(item.start.getHours() - 5, item.start.getMinutes() - 30)
          item.end = new Date(item.end);
          item.end.setHours(item.end.getHours() - 5, item.end.getMinutes() - 30)
          this.events.push(item)
        }
        //console.log(this.events);
        this.refresh.next();

      })
    } else {
      console.log('admin')
      this.appService.getAdminAllEvent(userId).subscribe((data: any) => {
        for (let item of data) {
          item['color'] = colors.blue;
          console.log('start-----', item.start);
          item.start = new Date(item.start);
          item.start.setHours(item.start.getHours() - 5, item.start.getMinutes() - 30)
          item.end = new Date(item.end);
          item.end.setHours(item.end.getHours() - 5, item.end.getMinutes() - 30)
          this.events.push(item)
        }
        //console.log(this.events);
        this.refresh.next();

      })
    }
  }
  //end

  dayClicked({ date, events }: { date: Date; events: CalendarEvent[] }): void {
    if (isSameMonth(date, this.viewDate)) {
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
      }
      this.viewDate = date;
    }
  }


  //function to show data using modal
  handleEvent(action: string, event: CalendarEvent): void {
    this.modalData = { event, action };
    this.modalRef = this.modalService.show(this.modalContent);

  }
  //end 

  //function to add event into calender
  public addEvent(): void {

    if (this.title == "" || this.title == null || this.title == undefined) {
      this.toastr.warningToastr('enter title')
      return
    } else if (this.startDate == null || this.startDate == undefined || this.startDate == "") {
      this.toastr.warningToastr('enter startdate');
      return
    } else if (this.startTime == null || this.startTime == "" || this.startTime == undefined) {
      this.toastr.warningToastr('please enter starttime')
      return
    } else if (this.endDate == null || this.endDate == undefined || this.endDate == "") {
      this.toastr.warningToastr('enter enddate');
      return
    } else if (this.endTime == null || this.endTime == "" || this.endTime == undefined) {
      this.toastr.warningToastr('please enter endtime');
      return
    }
    let checkDate = new Date();
    let oneDay = 24 * 60 * 60 * 1000;
    this.startDate.setHours(this.startTime.getHours(), this.startTime.getMinutes(), this.startTime.getSeconds());
    this.endDate.setHours(this.endTime.getHours(), this.endTime.getMinutes(), this.endTime.getSeconds());
    let noOfHours = Math.round(Math.abs((this.startDate - this.endDate) / oneDay));

    console.log('noooo', noOfHours)
    if (this.startDate < checkDate) {
      this.toastr.warningToastr('startDate should be greater than today')
      return;
    } else if (this.endDate < checkDate) {
      this.toastr.warningToastr('endDate should be greater than today')
      return;
    } else if (noOfHours >= 1) {
      this.toastr.warningToastr('meeting cannot be one day long');
      return;
    } else if (this.startDate > this.endDate) {
      this.toastr.warningToastr('enddate should be greater or equal to startdate');
      return;
    }
    this.events = [

      {
        title: this.title,
        start: this.startDate,
        end: this.endDate,
        color: colors.red,

      }
    ];
    let eventdata = {
      adminName: this.adminName,
      adminId: this.adminId,
      userId: this.userId,
      title: this.title,
      start: this.startDate,
      end: this.endDate,
      //color:colors.red
    }
    this.appService.addUserEvent(eventdata).subscribe(

      (apiResponse: any) => {
        if (apiResponse.error) {
          this.toastr.warningToastr(apiResponse.message);
        } else {
          this.toastr.successToastr("meeting scheduled");
          this.events = [];
          this.getUserAllEvent(this.userId);
        }
      },
      (error) => {
        this.toastr.errorToastr('Response not geting', error);
      }
    );

    this.socketService.setReminder(eventdata);
    let addEvent = { 'userId': this.userId, 'adminName': this.adminName, 'title': 'metting is scheduled' }
    this.socketService.informUserAboutMetting(addEvent);
    this.appService.informUserByMail(addEvent).subscribe(

      (Response: any) => {
        console.log('response', Response.error)
        if (Response.error == true) {
          this.toastr.warningToastr('Not able to send mail');
        } else {
          this.toastr.successToastr('mail is sent to user');
        }
      }
    );

  }
  //end

  //update event function
  public updateEvent(eventToUpdate: CalendarEvent) {
    let startDate = new Date(eventToUpdate.start)
    let endDate = new Date(eventToUpdate.end)
    console.log(startDate)
    if (eventToUpdate.start == null || eventToUpdate.start == undefined) {
      this.toastr.warningToastr('enter startdate');
      return
    } else if (eventToUpdate.end == null || eventToUpdate.end == undefined) {
      this.toastr.warningToastr('enter enddate');
      return
    } else if (startDate > endDate) {
      this.toastr.warningToastr('enddate should be greater or equal to startdate');
      return;
    }
    let eventdata = {
      _id: eventToUpdate["_id"],
      title: eventToUpdate["title"],
      start: eventToUpdate.start,
      end: eventToUpdate.end
    }
    this.appService.editmeeting(eventdata).subscribe(
      (apiResponse: any) => {
        if (apiResponse.error) {
          this.toastr.errorToastr(apiResponse.message);
        } else {
          this.toastr.successToastr("meeting edited");
          this.events = [];
          this.getUserAllEvent(this.userId);
        }
      },
      (error) => {
        this.toastr.errorToastr('Response not geting', error);
      }
    );
    let editEvent = { 'userId': this.userId, 'adminName': this.adminName, 'title': 'metting is edited' }
    this.socketService.informUserAboutMetting(editEvent);
    this.appService.informUserByMail(eventdata).subscribe(

      (Response: any) => {
        if (Response.error) {
          //this.toastr.warningToastr('Not able to send mail');
        } else {
          this.toastr.successToastr('mail is sent to user');
        }
      }
    );
  }
  //end

  //function to delete events from calender
  public deleteEvent(eventToDelete: CalendarEvent) {
    this.events = this.events.filter(event => event !== eventToDelete);
    //console.log(eventToDelete["_id"]);
    this.appService.deleteEvent(eventToDelete["_id"]).subscribe(

      (apiResponse: any) => {
        if (apiResponse.error) {
          this.toastr.errorToastr(apiResponse.message);
        } else {
          this.toastr.successToastr("meeting deleted");
          this.events = [];
          this.getUserAllEvent(this.userId);
        }
      },
      (error) => {
        this.toastr.errorToastr('Response not geting', error);
      }
    );
    let deleteEvent = { 'userId': this.userId, 'adminName': this.adminName, 'title': 'metting is deleted' }
    this.socketService.informUserAboutMetting(deleteEvent);
    this.appService.informUserByMail(deleteEvent).subscribe(

      (Response: any) => {
        if (Response.error) {
          //this.toastr.warningToastr('Not able to send mail');
        } else {
          this.toastr.successToastr('mail is sent to user');
        }
      }
    );

  }
  //end

  setView(view: CalendarView) {
    this.view = view;
  }

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }


}
