import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrManager } from 'ng6-toastr-notifications';
import { AppService } from 'src/app/app.service';
import { Cookie } from 'ng2-cookies/ng2-cookies';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  private email: any;
  private password: any;
  constructor(private router: Router,
    private appService: AppService,
    private toastr: ToastrManager) { }

  ngOnInit() {
    let authToken = Cookie.get('authToken');
    if (authToken != null) {
      let userDetail = this.appService.getUserInfoIntoLocalStorage();
      let isUserAdmin = (userDetail.isAdmin == "admin") ? true : false;
      if (!isUserAdmin) {
        this.router.navigate(['/normaluserdashboard'])
      } else {
        this.router.navigate(['/adminuserdashboard'])
      }
    }


  }
  //navigate to signup page
  public goToSignUp(): any {
    return this.router.navigate(['/signup']);
  }
  //navigate to recover password page
  public goToRecoverPassword(): any {
    return this.router.navigate(['/recoverpassword']);
  }

  //user signin function
  public signinFunction(): any {
    let data = {
      email: this.email,
      password: this.password
    }
    if (!this.email) {
      return this.toastr.warningToastr("enter email");
    }
    else if (!this.password) {
      return this.toastr.warningToastr("enter password");
    }
    this.appService.signinFunction(data).subscribe(
      (apiResponse: any) => {
        if (apiResponse) {
          console.log(apiResponse);
          Cookie.set('authToken', apiResponse.data.authToken);
          Cookie.set('receiverId', apiResponse.data.userDetails.userId);
          Cookie.set('receiverName', apiResponse.data.userDetails.firstName + ' ' + apiResponse.data.userDetails.lastName);
          this.appService.setUserInfoIntoLocalStorage(apiResponse.data.userDetails);
          if (apiResponse.data.userDetails.isAdmin == 'admin') {
            this.router.navigate(['../adminuserdashboard']);
          } else {
            this.router.navigate(['../normaluserdashboard']);
          }
        }
        else {
          this.toastr.warningToastr(apiResponse.message);
        }
      },
      (error: any) => {
        this.toastr.warningToastr("Incorrect email or password");
      }
    );
  }
}
