import { Component, OnInit } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { Router } from '@angular/router';
import { ToastrManager } from 'ng6-toastr-notifications';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Cookie } from 'ng2-cookies/ng2-cookies';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  public firstName: any;
  public lastName: any;
  public email: any;
  public mobileNumber: any;
  public password: any;
  public coutryCode: any;
  public isAdmin;
  public checkBox = false;
  public userName;
  public options: any = [];

  constructor(
    public appService: AppService,
    public router: Router,
    public toastr: ToastrManager,
    public http: HttpClient
  ) { }

  ngOnInit() {
    let authToken = Cookie.get('authToken');
    if (authToken != null) {
      let userDetail = this.appService.getUserInfoIntoLocalStorage();
      let isUserAdmin = (userDetail.isAdmin == "admin") ? true : false;
      if (!isUserAdmin) {
        this.router.navigate(['/normaluserdashboard'])
      } else {
        this.router.navigate(['/adminuserdashboard'])
      }
    }
    this.getJSON().subscribe(data => {
      this.options = data;
      console.log(this.options);
    });
    this.isAdmin = '';
  }
  //navigate to signin page
  public goToSignIn(): any {
    this.router.navigate(['/']);
  }
  //get countries name from json data
  public getJSON(): Observable<any> {
    return this.http.get("assets/names.json");
  }

  //checkbox change funtion
  public changeCheckBox(val): any {
    //console.log(this.checkBox)
    if (this.checkBox == true) {
      this.isAdmin = "admin";
    } else {
      this.isAdmin = '';
    }
    console.log(this.isAdmin)
  }

  //signup function
  public signupFunction(): any {
    if (!this.firstName) {
      this.toastr.warningToastr("enter Firstname");
    }
    else if (!this.lastName) {
      this.toastr.warningToastr("enter Lastname");
    }
    else if (!this.email) {
      this.toastr.warningToastr("enter email");
    }
    else if (!this.mobileNumber) {
      this.toastr.warningToastr("enter mobileNumber");
    }
    else if (!this.password) {
      this.toastr.warningToastr("enter password");
    }
    else if (!this.coutryCode) {
      this.toastr.warningToastr("enter coutryCode");
    }
    else if (!this.userName) {
      this.toastr.warningToastr('enter username');
    }
    else if (!this.userName.endsWith('admin') && this.isAdmin == "admin") {
      this.toastr.warningToastr('for admin username should end with admin');
    }
    else {
      let data = {
        firstName: this.firstName,
        lastName: this.lastName,
        email: this.email,
        mobileNumber: this.mobileNumber,
        password: this.password,
        coutryCode: this.coutryCode,
        userName: this.userName,
        isAdmin: this.isAdmin
      }
      this.appService.signupFunction(data).subscribe(

        (apiResponse: any) => {
          if (apiResponse.error) {
            this.toastr.warningToastr(apiResponse.message);
          } else {
            this.toastr.successToastr("signUp successful");
            setTimeout(() => {
              this.goToSignIn();
            }, 2000);
          }


        },
        (error) => {
          this.toastr.errorToastr('Response not geting', error);
        }
      );
    }
  }
}
